 <template>
    <el-row :gutter="10">
    <el-col
      v-for="(o,index) in types"
      :key="o"
      :span="12"
      style="margin-top:20px;"
    >
      <el-card @click="cardClick(o,index)" :body-style="{  padding: '0px' }">
        <img
          :src="o.url"
          class="image"
        />
        <div style="padding: 14px">
          <span>{{o.title}}</span>
          <div class="bottom">
            <time class="time">{{o.title}}：{{ o.desc }}</time>
            <el-button text class="button">更多产品</el-button>
          </div>
        </div>
      </el-card>
    </el-col>
  </el-row>
 </template>
<script>

export default {
  components:{
     
  },
  name: 'home',
  data(){
    return {
      types:[
        {title:'大板',url:'/Images/Types/typ1.jpeg',desc:'描述信息',index:1},
        {title:'沙发',url:'/Images/Types/typ2.jpeg',desc:'描述信息',index:2},
        {title:'茶几',url:'/Images/Types/typ3.jpeg',desc:'描述信息',index:3},
        {title:'板材',url:'/Images/Types/typ4.jpeg',desc:'描述信息',index:4},
      ]
    }
  },
  methods:{
    cardClick:function(item,index){
      console.log(item,index);
      this.$router.push({name:'typeList',params: { dicIndex: item.index }});
    }
  }
}
</script>

<style>
.time {
  font-size: 12px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.button {
  padding: 0;
  min-height: auto;
}

